.updateProfileContainer {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(231, 231, 231);
  position: fixed;
  top: 0;
  left: 0;
}

.updateProfileBox {
  background-color: #fff;
  width: 25vw;
  height: 70vh;
  box-sizing: border-box;
  overflow: hidden;
}

.updateProfileHeading{
    text-align: center;
    color: rgba(0, 0, 0, 0.623);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.219);
    width: 50%;
    margin: auto;
}
.updateProfileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  height: 70%;
  justify-content: space-evenly;
  transition: all 0.5s;
}

.updateProfileForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}

.updateProfileForm > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.updateProfileForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.687);
}

#updateProfileImage > img {
  width: 4vmax;
  height: 4vmax;
  border-radius: 100%;
  padding: 0.5vmax;
  background-color: rgba(204, 204, 204, 0.532);
  margin-right: 1vmax;
}

#updateProfileImage > input {
  display: flex;
  padding: 0;
}

#updateProfileImage > input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0;
  font: 400 0.8vmax cursive;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: #fff;
}

#updateProfileImage > input::file-selector-button:hover {
  background-color: rgb(230, 225, 225);
}
.updateProfileBtn {
  border: none;
  background-color: tomato;
  color: #fff;
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.updateProfileBtn:hover {
  background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
  .updateProfileContainer {
    background-color: #fff;
  }

  .updateProfileBox {
    width: 100vw;
    height: 95vh;
  }

  .updateProfileForm {
    padding: 5vmax;
  }

  .updateProfileForm > div > input {
    padding: 2.5vmax 7vmax;
    font: 300 2vmax cursive;
  }
  .updateProfileForm > div > svg {
    font-size: 2.8vmax;
    padding: 0 1.5vmax;
  }
  .updateProfileForm > a {
    font: 500 2vmax "Gill Sans";
  }
  #updateProfileImage > img {
    width: 9vmax;
    border-radius: 100%;
    margin-right: 1vmax;
  }

  #updateProfileImage > input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
  }
  .updateProfileBtn {
    font: 300 2vmax "Roboto";
    padding: 2vmax;
  }
}
